import React, { useEffect, useState } from 'react';
import defaultData from '../defaultData';
import { animateScroll as scroll, scroller } from 'react-scroll';
import SvgLogo from '../SvgLogo';

interface NavbarProps extends React.HTMLProps<HTMLDivElement> {}

const Navbar: React.FC<NavbarProps> = props => {
  const [duration, setDuration] = useState(500);
  // @TODO 動的に取得したかったがうまくいかなかったため決め打ち
  const [navbarHeight, setNavbarHeight] = useState(52.75);
  const [shouldChangeNavbarColor, setShouldChangeNavbarColor] = useState(false);

  const updateNavbarColor = () => {
    const scrollY: number =
      document.documentElement.scrollTop || window.pageYOffset;
    setShouldChangeNavbarColor(window.innerHeight - navbarHeight - 1 < scrollY);
  };

  const scrollToTop = () => {
    scroll.scrollToTop({
      duration
    });
  };

  const scrollTo = (element: string) => {
    scroller.scrollTo(element, {
      duration,
      smooth: true,
      offset: navbarHeight * -1
    });
  };

  useEffect(() => {
    window.addEventListener('scroll', updateNavbarColor);
    updateNavbarColor();

    return () => {
      // unmount
      window.removeEventListener('scroll', updateNavbarColor);
    };
  }, []);

  return (
    <nav
      className={`navbar navbar-expand-lg fixed-top ${
        shouldChangeNavbarColor ? 'navbar-light navbar--changed' : 'navbar-dark'
      }`}
    >
      <div className='container'>
        <a
          className='navbar-brand'
          onClick={scrollToTop}
          style={{
            cursor: 'pointer'
          }}
        >
          <SvgLogo />
        </a>

        <button
          className='navbar-toggler'
          type='button'
          data-toggle='collapse'
          data-target='#navbarSupportedContent'
          aria-controls='navbarSupportedContent'
          aria-expanded='false'
          aria-label='Toggle navigation'
        >
          <span className='navbar-toggler-icon'></span>
        </button>

        <div className='collapse navbar-collapse' id='navbarSupportedContent'>
          <ul className='navbar-nav mr-auto'>
            {defaultData.navbar.map((d: any, i: number) => (
              <li className='nav-item mx-1' key={i}>
                <a
                  className='nav-link mb-0'
                  onClick={() => scrollTo(d.target)}
                  style={{
                    cursor: 'pointer'
                  }}
                >
                  {d.label}
                </a>
              </li>
            ))}
            <li className='nav-item mx-1'>
              <a
                className='nav-link mb-0 ml-2 px-4 btn btn-primary rounded'
                onClick={() => scrollTo('contact')}
                style={{
                  cursor: 'pointer'
                }}
              >
                <i className='far fa-envelope fa-sm mr-2' />
                お問い合わせ
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
