import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

const pcVideo = require('../../images/main-movie-pc.mp4');
const spVideo = require('../../images/main-movie-sp.mp4');
const pcPoster = require('../../images/main-movie-pc-thumb.jpg');
const spPoster = require('../../images/main-movie-sp-thumb.jpg');

interface MainvisualProps extends React.HTMLProps<HTMLDivElement> {}

interface MainvisualState {
  clientWidth: number;
  clientHeight: number;
  videoSrc: string;
  videoPoster: string;
  videoWidth: number;
  videoHeight: number;
  displayVideoWidth: number;
  displayVideoHeight: number;
  marginLeft: any;
  isDisplaySlogan: boolean;
}

export default class extends React.Component<MainvisualProps, MainvisualState> {
  constructor(props) {
    super(props);

    this.state = {
      clientWidth: 0,
      clientHeight: 0,
      videoSrc: '',
      videoPoster: '',
      videoWidth: 0,
      videoHeight: 0,
      displayVideoWidth: 0,
      displayVideoHeight: 0,
      marginLeft: 0,
      isDisplaySlogan: true
    };

    this.handleScrollWindow = this.handleScrollWindow.bind(this);
    this.handleResizeWindow = this.handleResizeWindow.bind(this);
    this.resizeVideo = this.resizeVideo.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScrollWindow);
    window.addEventListener('resize', this.handleResizeWindow);
    this.updateSlogan();
    this.resizeVideo();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScrollWindow);
    window.removeEventListener('resize', this.handleResizeWindow);
  }

  handleScrollWindow() {
    this.updateSlogan();
  }

  handleResizeWindow() {
    this.resizeVideo();
    // リサイズ時にサイズが取れていない場合があるため念のためディレイ実行もさせる
    setTimeout(this.resizeVideo, 1000);
  }

  updateSlogan() {
    const scrollY: number =
      document.documentElement.scrollTop || window.pageYOffset;

    this.setState({
      isDisplaySlogan: scrollY < window.outerHeight + 1
    });
  }

  resizeVideo() {
    /*
    const clientWidth: number = Math.max(document.documentElement.clientWidth, window.innerWidth)
    const clientHeight: number = Math.max(document.documentElement.clientHeight, window.innerHeight)
     */

    // Safari のバーも含めて計算したいため
    const clientWidth: number = window.outerWidth;
    const clientHeight: number = window.outerHeight;

    let videoSrc: string,
      videoPoster: string,
      videoWidth: number,
      videoHeight: number,
      displayVideoWidth: number,
      displayVideoHeight: number,
      marginLeft: any;

    const isLandscape: boolean = clientHeight < clientWidth;

    videoSrc = isLandscape ? pcVideo : spVideo;
    videoPoster = isLandscape ? pcPoster : spPoster;
    videoWidth = isLandscape ? 1280 : 320;
    videoHeight = isLandscape ? 720 : 440;

    if (clientWidth / clientHeight < videoWidth / videoHeight) {
      // ウィンドウの方が高さが余るため、動画の縦を基準にする
      displayVideoHeight = clientHeight;
      displayVideoWidth = (displayVideoHeight / videoHeight) * videoWidth;
      marginLeft = (displayVideoWidth - clientWidth) * -0.5;
    } else {
      // ウィンドウの方が左右が余るため、動画の横を基準にする
      displayVideoWidth = clientWidth;
      displayVideoHeight = (displayVideoWidth / videoWidth) * videoHeight;
      marginLeft = 0;
    }

    const floor: (value: number, digit?: number) => number = (
      value,
      digit = 10
    ) => Math.floor(value * digit) / digit;

    displayVideoWidth = floor(displayVideoWidth);
    displayVideoHeight = floor(displayVideoHeight);
    marginLeft = floor(marginLeft);

    this.setState({
      clientWidth,
      clientHeight,
      videoSrc,
      videoPoster,
      videoWidth,
      videoHeight,
      displayVideoWidth,
      displayVideoHeight,
      marginLeft
    });
  }

  public render() {
    return (
      <div
        className='mainvisual'
        style={{
          // 100vw, 100vh 指定だと Safari のバーを含めて計算してしまうためJSで
          /*
          width: this.state.clientWidth,
          height: this.state.clientHeight
           */
          width: '100vw',
          height: '100vh'
        }}
      >
        <div className='container'>
          <h1
            className={`mainvisual__slogan text-white${
              this.state.isDisplaySlogan ? '' : ' d-none'
            }`}
          >
            <strong>コンパクトマンション</strong>
            <br />
            という選択を
          </h1>
        </div>
        <video
          style={{
            width: this.state.displayVideoWidth,
            height: this.state.displayVideoHeight,
            left: this.state.marginLeft
          }}
          className='mainvisual__video'
          src={this.state.videoSrc}
          poster={this.state.videoPoster}
          width={this.state.videoWidth}
          height={this.state.videoHeight}
          autoPlay={true}
          muted={true}
          loop={true}
          playsInline={true}
        ></video>
      </div>
    );
  }
}
